
.App {
  text-align: center;
  height: 95vh;
  width: 99vw;
  display: flex;
  flex-flow: nowrap;
}

.sidebar {
  display: block;
  width: 24%;
}

.title {
  border: 0.2em solid black;
}

.venuebar {
  background-color: blue;
  color: white;
  border: 0.2em solid black;
  margin: 1em auto;
  border-radius: 1em;
}

.venueList {
  padding: 0;
}

.listItem {
  display: flex;
  flex-flow: column;
  background-color: darkmagenta;
  font-size: 1em;
  color: white;
  margin: 2em auto;
  padding: 0.5em;
  border-radius: 1em;
  border: 0.1em solid black;
  cursor: pointer;
}

li:hover {
  background-color: blue;
}

.modal {
  position: fixed;
  z-index: 1010;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  margin: auto;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgb(0, 0, 0);
  background-color: rgba(0, 0, 0, 0.4);
}

.display-block {
  display: block;
}

.display-none {
  display: none;
}

.modal-content {
  background-color: blue;
  color: white;
  margin: 15% auto;
  padding: 20px;
  border: 1px solid black;
  width: 60%;
  z-index: 1000;
}

.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

button {
  padding: 5%;
  margin: 10%;
  border-radius: 10%;
  font-size: 1.5em;
}

button:hover {
  background-color: blue;
  color: black;
}

.filterButton {
  font-size: 1em;
  padding: 0.5em;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

